import React from 'react';
import SLDSSection from './SLDSSection';
import GRAXLoggingDataTable from './GRAXLoggingDataTable';
import GRAXFilterGeneration from './GRAXFilterGeneration';
import GRAXFieldsCombobox from './GRAXFieldsCombobox';

import {Button} from '@salesforce/design-system-react';

import {$, baseUrl, GetParams} from '../lib/grax-data-lake';

import _ from 'lodash';
import GRAXRestoreStatusTable from "./GRAXRestoreStatusTable";

const ELASTIC_SORTABLE_TYPES = ['text', 'date', 'number'];

export default class GRAXLogging extends React.Component {
    constructor(props) {
        super(props);

        this._isMounted = false;

        this.state = {
            retrieved: false,
            retrievedCounter: 0,
            _graxFields: null,
            _graxFieldsSelection: null,
            _graxFieldsSelectionList: this.props.columns || ['Id'],
            _filterFunction: ((dataList) => dataList),
            _getFilterQuery: (() => {}),
            _filterValuesFields: [],
            _graxSortableFields: null
        }
    }

    getFilterQuery(_getFilterQuery) {
        var q = 'doc.attributes.type.keyword: "grax_logging';

        if(this.props.subtype) {
            q += ('+' + this.props.subtype);
        }

        q += '"';

        var _q = _getFilterQuery();
        if(_q) {
            q += (' AND (' + _q + ')')
        }

        return q;
    }

    hasFieldSelected() {
        return this.state._graxFields != null && this.state._graxFieldsSelectionList != null && this.state._graxFieldsSelectionList.length > 0;
    }

    componentDidMount() {
        this._isMounted = true;

        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/getDataTypeFields/graxgrax_loggingindex' + GetParams({action:'load'}),
            data: {},
            success: function(data) {
                if(this._isMounted) {
                    this.setState({
                        _graxFields: _.map(this.state._graxFieldsSelectionList, (val, i) => ({
                            id: val,
                            label: val,
                            type: data[val]
                        })),
                        retrieved: true
                    });
                }
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                if(this._isMounted) {
                    this.setState({
                        retrieved: true,
                        error: err.toString()
                    });
                }
            }.bind(this)
        });
    }

    render() {
        return (
            <div>
                <div style={{display: 'none'}}>
                    <GRAXFieldsCombobox
                        dataIndex="graxgrax_loggingindex"
                        onLoad={(e, _graxFieldsSelection, _graxFields, _graxFieldsSelectionList) => {
                            console.log('onLoad', _graxFieldsSelection, _graxFieldsSelectionList, _graxFields);

                            this.setState({
                                _graxFieldsSelection: _graxFieldsSelection,
                                _graxSortableFields: _.reduce(_graxFields, (sortableFields, elasticField, i) => {
                                    if(ELASTIC_SORTABLE_TYPES.indexOf(elasticField.type) !== -1) {
                                        sortableFields.push(elasticField.id);
                                    }

                                    return sortableFields;
                                }, [])
                            });
                        }}
                        cookieSubfix={this.props.subtype || 'logs'}
                    />
                </div>

               { this.props.subtype === 'restore' &&
                  <SLDSSection isCollapsible={true} isOpen={true} title="Restore Status">
                      <GRAXRestoreStatusTable/>
                  </SLDSSection>
                 }
                <SLDSSection isCollapsible={true} isOpen={true} title="Filter Logs">
                    {this.state._graxFieldsSelectionList != null && (
                        <GRAXFilterGeneration
                            filterableFieldOptions={this.state._graxFields}
                            onFilterValueChangeHandler={(_filterFunction, _filterValuesFields, _getFilterQuery) => {
                                this.setState({
                                    retrievedCounter: this.state.retrievedCounter,
                                    _filterFunction: _filterFunction,
                                    _getFilterQuery: _getFilterQuery,
                                    _graxFieldsSelection: this.state._graxFieldsSelection,
                                    _graxFieldsSelectionList: this.state._graxFieldsSelectionList,
                                    _filterValuesFields: _filterValuesFields
                                });
                            }}
                        />
                    )}

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="GRAX Logs">
                    <div className="-x-small-buttons--horizontal slds-p-bottom_large">
					    <Button label="Retrieve" variant="brand" onClick={() => {
                            this.setState({
                                retrievedCounter: this.state.retrievedCounter + 1
                            })
                        }} />
                    </div>

                    <GRAXLoggingDataTable
                        key={'GRAXLoggingDataTable_' + this.state.retrievedCounter}
                        renderKey={'GRAXLoggingDataTable_' + this.state.retrievedCounter}
                        columns={this.state._graxFieldsSelectionList}
                        _graxFieldsSelection={this.state._graxFieldsSelection}
                        sortableColumns={this.state._graxSortableFields}
                        hiddenColumns={this.state._filterValuesFields}
                        dataIndex="graxgrax_loggingindex"
                        filterFunction={this.state._filterFunction}
                        filterQuery={this.getFilterQuery(this.state._getFilterQuery)}
                    />
                </SLDSSection>
            </div>
        );
    }
}
