import React from "react";

import {
  DataTable,
  DataTableColumn,
  ScopedNotification,
  Button,
  ButtonGroup
} from "@salesforce/design-system-react";
import { moment } from "../lib/utils";
import { $, baseUrl } from "../lib/grax-data-lake";

const TS_FORMAT = "L LT";
const TIME_INTERNAL_DAYS = 7;
const PAGE_SIZE = 25;

const columns = [
  {
    key: "num-records",
    property: "total",
    label: "# of Records"
  },
  {
    key: "obj-type",
    property: "objectType",
    label: "Object Type"
  },
  {
    key: "queued",
    property: "statusQueued",
    label: "Queued"
  },
  {
    key: "done",
    property: "statusDone",
    label: "Done"
  },
  {
    key: "completed",
    property: "completed",
    label: "% Completed"
  },
  {
    key: "start-time",
    property: "strStartDate",
    label: "Start Time"
  },
  {
    key: "end-time",
    property: "strEndDate",
    label: "End Time"
  }
];

export default class GRAXRestoreStatusTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pageNum: 0,
      items: [],
      sortColumnDirection: {}
    };
    this.allItems = [];
  }

  componentDidMount() {
    this.loadData();
  }

  onDataLoaded = data => {
    if (Array.isArray(data)) {
      // pre-process raw data coming in
      data.forEach(record => {
        // eslint-disable-next-line no-param-reassign
        record.completed = `${Math.round((record.statusDone * 100) / record.total)}%`;
        // eslint-disable-next-line no-param-reassign
        let m = record.startDate ? moment(record.startDate) : undefined;
        // eslint-disable-next-line no-param-reassign
        record.strStartDate = m ? m.format(TS_FORMAT) : "";
        // eslint-disable-next-line no-param-reassign
        record.unixStateDate = m ? m.unix() : Number.MAX_SAFE_INTEGER;
        // eslint-disable-next-line no-param-reassign
        m = record.endDate ? moment(record.endDate) : undefined;
        // eslint-disable-next-line no-param-reassign
        record.strEndDate = m ? m.format(TS_FORMAT) : "";
        // eslint-disable-next-line no-param-reassign
        record.unixEndDate = m ? m.unix() : Number.MAX_SAFE_INTEGER;
      });
      this.allItems = data;
      this.sortData("total", "desc");
    } else {
      this.allItems = [];
    }
  };

  onDataLoadError = (xhr, status, err) => {
    this.setState({
      error: err.toString()
    });
  };

  loadData = () => {
    this.setState({
      pageNum: 0,
      items: [],
      sortColumn: "total",
      sortColumnDirection: { total: "desc" }
    });

    $.ajax({
      type: "GET",
      url: `${baseUrl}/grax-app/grax-data-lake/asyncQueue/status`,
      data: {
        days: TIME_INTERNAL_DAYS
      },
      success: this.onDataLoaded,
      error: this.onDataLoadError
    });
  };

  updatePage = pageNum => {
    this.setState({
      pageNum,
      items: this.allItems.slice(pageNum * PAGE_SIZE, (pageNum + 1) * PAGE_SIZE)
    });
  };

  nextPage = () => {
    const { pageNum } = this.state;
    if ((pageNum + 1) * PAGE_SIZE <= this.allItems.length) {
      this.updatePage(pageNum + 1);
    }
  };

  prevPage = () => {
    const { pageNum } = this.state;
    if (pageNum > 0) {
      this.updatePage(pageNum - 1);
    }
  };

  sortData = (sortProperty, sortDirection) => {
    this.allItems.sort((a, b) => {
      let property = sortProperty;
      if (sortProperty === "strStartDate") {
        property = "unixStateDate";
      } else if (sortProperty === "strEndDate") {
        property = "unixEndDate";
      }

      let x;
      let y;
      if (property === "completed") {
        x = Math.round((a.statusDone * 100) / a.total);
        y = Math.round((b.statusDone * 100) / b.stotal);
      } else {
        x = a[property];
        y = b[property];
      }

      let val = 0;

      if (x > y) {
        val = 1;
      }
      if (x < y) {
        val = -1;
      }

      if (sortDirection === "desc") {
        val *= -1;
      }

      return val;
    });

    this.setState({
      sortColumn: sortProperty,
      sortColumnDirection: {
        [sortProperty]: sortDirection
      }
    });
    this.updatePage(0);
  };

  handleSort = (sortColumn, ...rest) => {
    console.log(`sort column = ${sortColumn}`);

    const sortProperty = sortColumn.property;
    const { sortDirection } = sortColumn;
    this.sortData(sortProperty, sortDirection);
  };

  render() {
    const { items, error, pageNum, sortColumn, sortColumnDirection } = this.state;
    return (
      <>
        <div className="-x-small-buttons--horizontal slds-p-bottom_large">
          <Button label="Retrieve" variant="brand" onClick={this.loadData} />
        </div>
        {error && (
          <div style={{ clear: "both" }}>
            <ScopedNotification theme="dark">
              <p>{error}</p>
            </ScopedNotification>
          </div>
        )}
        {items.length > 0 && (
          <>
            <DataTable
              assistiveText={{
                actionsHeader: "actions",
                columnSort: "sort this column",
                columnSortedAscending: "asc",
                columnSortedDescending: "desc"
              }}
              style={{ marginBottom: "5px" }}
              items={items}
              fixedLayout
              id="testtable"
              onSort={this.handleSort}
            >
              {columns.map(col => (
                <DataTableColumn
                  key={col.key}
                  property={col.property}
                  label={col.label}
                  isSorted={sortColumn === col.property}
                  sortable
                  sortDirection={sortColumnDirection[col.property]}
                />
              ))}
            </DataTable>
            {PAGE_SIZE < this.allItems.length && (
              <ButtonGroup>
                <Button
                  iconCategory="utility"
                  iconName="chevronright"
                  iconPosition="right"
                  variant="neutral"
                  disabled={pageNum === 0}
                  label={`previous ${PAGE_SIZE}`}
                  onClick={this.prevPage}
                />
                <Button
                  iconCategory="utility"
                  iconName="left"
                  iconPosition="left"
                  variant="neutral"
                  disabled={(pageNum + 1) * PAGE_SIZE >= this.allItems.length}
                  label={`next ${PAGE_SIZE}`}
                  onClick={this.nextPage}
                />
              </ButtonGroup>
            )}
          </>
        )}
      </>
    );
  }
}
